{$('.scrollTo').on('click', function() { // Au clic sur un élément
            var page = $(this).attr('href'); // Page cible
            var speed = 750; // Durée de l'animation (en ms)
            $('html, body').animate( { scrollTop: $(page).offset().top }, speed ); // Go
            return false;});
            };

      { // pour l'ouverture du menu
      const toggle = document.querySelector('#menu')
      const visible = document.querySelector('#collapse')

      toggle.addEventListener('click',()=>{
        visible.classList.toggle('collapse')
      })
      const lm = document.querySelector('#lm')
      const le1 = document.querySelector('#le1')
      const tlc = document.querySelector('#tlc')
      const le2 = document.querySelector('#le2')
      const wrap1 = document.querySelector('#wrap1')
      const wrap2 = document.querySelector('#wrap2')
      const wrap3 = document.querySelector('#wrap3')
      const wrap4 = document.querySelector('#wrap4')
      const mnu = document.querySelector('#menu')

      mnu.addEventListener('click',()=>{
      lm.classList.toggle('load_main');
      le1.classList.toggle('loader_element1');
      tlc.classList.toggle('texte_loader_center');
      le2.classList.toggle('loader_element2');
      wrap1.classList.toggle('wrap1');
      wrap2.classList.toggle('wrap2');
      wrap3.classList.toggle('wrap3');
      wrap4.classList.toggle('wrap4');
      })
    };

{$(".hide_menu").on('click', function(){$("#menu").trigger("click")})} // pour la fermeture apres click du menu

     { // pour l'animation du cercle du bouton principal
        const add = document.querySelector('#declenche_cercle')
        const visible = document.querySelector('#anim_cercle')

        add.addEventListener('mouseenter',()=>{
          visible.classList.add('anim_cercle')
        })

        const remove2 = document.querySelector('#declenche_cercle')
        const visible3 = document.querySelector('#anim_cercle')

        remove2.addEventListener('mouseleave',()=>{
        visible3.classList.add('anim_cercle2')
        visible3.classList.remove('anim_cercle')

        setTimeout(function(){visible3.classList.remove('anim_cercle2')}, 380);
        })
      };


      { // pour l'animation de la fleche

        const line = document.querySelector('.declenche_line')
        const line_red = document.querySelector('#line_red')

        line.addEventListener('mouseenter',()=>{
        line_red.classList.add('line_red3')
        })
        const line_red2 = document.querySelector('.declenche_line')
        const line_red3 = document.querySelector('#line_red')

        line_red2.addEventListener('mouseleave',()=>{
        line_red3.classList.add('line_red2')
        line_red3.classList.remove('line_red3')

        setTimeout(function(){line_red3.classList.remove('line_red2')}, 400);
        })
      };
                       
